@import url(https://fonts.googleapis.com/css2?family=Noto + Sans + KR:wght@100;300;400;500;700;900&display=swap);

html {
  display: block;
}

body {
  background-color: #000;
}

body,
input,
textarea,
select,
table,
button,
code {
  color: #111;
  letter-spacing: -0px;
  font-size: 14px;
  line-height: 22px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  text-align: left;
}

body,
a,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
tr,
th,
td,
form,
fieldset,
label,
legend,
input,
textarea,
button,
menu,
div,
textarea,
select,
table,
button,
code {
  margin: 0px;
  padding: 0px;
  -webkit-text-size-adjust: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #f2951c;
  transition: all 0.9s ease-out;
}

a:hover {
  color: white;
}

::-webkit-scrollbar {
  width: 5px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background: #f2951c;
  border-radius: 4px;
}

header {
  background-color: #000;
  padding: 30px;
  text-align: center;
  font-size: 30px;
  color: #f2951c;
}

h2,
h3 {
  color: #f2951c;
  font-family: "Roboto", Sans-serif;
  font-weight: 600;
}
h3 {
  margin-bottom: -12px;
}
input[type="text" i] {
  padding: 1px 2px;
}

input[type="text"] {
  writing-mode: horizontal-tb !important;
  text-rendering: auto;
  letter-spacing: normal;
  word-spacing: normal;
  line-height: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  appearance: auto;
  -webkit-rtl-ordering: logical;
  cursor: text;
  margin: 0em;
  padding: 12px 12px;
  border-width: 2px;
  border-style: inset;
  border-image: initial;
}

input::placeholder {
  opacity: 0.5;
  color: white;
}

.sHeader {
  width: 100%;
  height: 70px;
  position: fixed;
  top: 0px;
  left: 0px;
}

div {
  display: block;
}

.sRaritysection .sWrapper {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  padding: 400px 0px 0px 0px;
}

.sWrapper {
  position: absolute;
  width: 100%;
  margin: 0 auto;
}

.searchBar {
  width: 100%;
  height: 180px;
  position: absolute;
  top: 50px;
  left: 0px;
  padding-top: 40px;
}

.sTitle {
  font-size: 17px;
  font-weight: 800;
  text-align: center;
  background: #f2951c;
  display: inline-block;
  padding: 0px 20px;
  margin-bottom: 50px;
  height: 25px;
  line-height: 25px;
}

.sMobile {
  display: block;
  font-family: "Roboto", Sans-serif;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 16px;
  text-align: center;
  color: #f2951c;
  line-height: 6px;
  visibility: hidden;
}

.sRaritysection .sTxt1 {
  display: block;
  font-family: "Roboto", Sans-serif;
  font-size: 48px;
  font-weight: 600;
  margin-bottom: 60px;
  text-align: center;
  color: #f2951c;
  line-height: 60px;
}

.sTxt1 {
  word-break: keep-all;
}
.sTxt2 {
  margin-bottom: 20px;
}
.searchNftwrap {
  display: inline-block;
  width: 260px;
  height: 70px;
  position: relative;
}

.inputSearch {
  width: 100%;
  height: 38px;
  background: #1f1f1f;
  border: 2px solid #f2951c;
  padding: 10px 40px 0px 10px;
  font-size: 17px;
  color: #f2951c;
  border-radius: 6px;
}

.buttonSearch {
  position: absolute;
  z-index: 42;
  width: 35px;
  height: 35px;
  top: 1px;
  right: 0px;
  /* background: url(./static/search-no-fff.png) center no-repeat; */
  background-size: 19px 19px;
  opacity: 0.5;
}

.collection {
  width: 100%;
  max-width: 1920px;
  margin: 50px auto;
  border: #f2951c solid 0px;
}

.collection:after {
  content: "";
  display: block;
  clear: both;
}

.surface-containerLeft {
  float: left;
  position: relative;
  width: 11%;
  min-width: 150px;
  border: #f2951c solid 0px;
  margin-right: auto;
  margin-left: auto;
}

.container {
  float: left;
  position: relative;
  width: 78%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  border: #f2951c solid 0px;
  margin: 0 auto;
}

.surface-containerRight {
  float: left;
  position: relative;
  width: 11%;
  min-width: 150px;
  margin-right: auto;
  margin-left: auto;
  border: #f2951c solid 0px;
}

.item {
  flex-basis: 0;
  margin-bottom: 20px;
  position: relative;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
  border-radius: 4px;
  padding: 0 5px;
  width: 100%;
  margin: 0 auto;
}

.item__img__img {
  max-width: 190px;
  max-height: 190px;

  border: solid 1px #f2951c;
  border-radius: 4px;
}
.item__img {
  border: 0;
  border-radius: 0px;
}

.item__img:hover {
  opacity: 0.5;
}
.item__rank {
  position: absolute;
  background-color: #f2951c;
  border-radius: 50%;
  font-size: 12px;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  top: -12px;
  right: -6px;
  font-weight: bold;
  -webkit-box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.4);
  color: #000;
}

.item__name,
.item__ordinal {
  display: block;
  font-size: 14px;
  font-weight: bold;
  padding: 0 2px;
  margin: 0;
  text-decoration: none;
  color: #f2951c;
}
.item__status {
  display: block;
  font-size: 12px;
  color: #f2951c;
  padding: 5px 2px;
  margin: 0px 2px 0;
  text-decoration: none;
}

.item__link:hover {
  text-decoration: none;
}

.ias-foot {
  text-align: center;
  margin: 20px 0;
  font: 12px sans-serif;
  color: #aaa;
}

.ias-foot a {
  color: #999;
}

.greyGridTable {
  right: 0;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  background-color: #000000;
  text-align: left;
  border-collapse: collapse;
  width: 150px;
}
.tableRight {
  margin-left: auto;
  margin-right: 0;
}

.divTable.greyGridTable .divTableCell,
.divTable.greyGridTable .divTableHead {
  padding: 3px 4px;
}
.divTable.greyGridTable .divTableBody .divTableCell {
  font-size: 12px;
  color: #f2951c;
}
.divTable.greyGridTable .divTableHeading {
  background: #000000;
  border-bottom: 1px solid #f2951c;
}
.divTable.greyGridTable .divTableHeading .divTableHead {
  font-size: 14px;
  font-weight: bold;
  color: #f2951c;
  text-align: left;
}
.greyGridTable .tableFootStyle {
  font-size: 12px;
}
/* DivTable.com */
.divTable {
  display: table;
}
.divTableRow {
  display: table-row;
}
.divTableHeading {
  display: table-header-group;
}
.divTableCellRight {
  font-size: 12px;
  color: #f2951c;
  display: table-cell;
  text-align: right;
}

.divTableCellLeft {
  display: table-cell;
}

.divTableHead {
  display: table-cell;
}

.divTableHeading {
  display: table-header-group;
}
.divTableFoot {
  display: table-footer-group;
}
.divTableBody {
  display: table-row-group;
}
@media screen and (max-width: 1600px) {
  .cointainer .sWrapper {
    width: 83.3%;
    max-width: 1600px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (max-width: 1280px) {
  .cointainer .sWrapper {
    width: 66.7%;
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (max-width: 1024px) {
  .cointainer .sWrapper {
    width: 53.3%;
    max-width: 1024px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media screen and (max-width: 600px) {
  .cointainer .sWrapper {
    float: none;
    width: 31.25%;
    max-width: 600px;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    clear: both;
  }
  @media screen and (max-width: 480px) {
    .cointainer .sWrapper {
      float: none;
      width: 25%;
      max-width: 480px;
      margin-top: 30px;
      margin-left: auto;
      margin-right: auto;
      clear: both;
    }
  }
  .surface-containerLeft {
    display: none;
  }
  .surface-containerRight {
    display: none;
  }
  .sMobile {
    display: block;
    font-family: "Roboto", Sans-serif;
    font-size: 12px;
    font-weight: 600;
    margin-bottom: 16px;
    text-align: center;
    color: #f2951c;
    line-height: 6px;
    visibility: visible;
  }
}
